import React, { Component } from 'react';
import './App.css';
import axios from 'axios';
import xml2js from 'xml2js';
import '../node_modules/font-awesome/css/font-awesome.min.css'; 

class App extends Component {
  constructor(props){
    super(props);
    this.state = {
      playbackUrl:'',
      playbackStation:'',
      playing: false,
      buffering: false,
      currentlyPlaying:'',
      timeElapsed:'',
      soma_xml_url:'https://somafm.com/channels.xml',
      urlList: []
    }   
  }

  componentDidMount(){    
    this.populateUrlsFromSoma();
  }

  populateUrlsFromSoma = () => {
    let url_list_arr = []
    axios
    .get(this.state.soma_xml_url)
    .then((resp) => {
      xml2js.parseString(resp.data, (err, result) => {
        for(let x in result.channels.channel){
          let name = result.channels.channel[x].title[0]
          let url = result.channels.channel[x].fastpls[0]._
          let imgurl = result.channels.channel[x].largeimage
          let listeners = result.channels.channel[x].listeners
          let description = result.channels.channel[x].description
          let currently_playing = result.channels.channel[x].lastPlaying[0]

          url_list_arr.push({
            'name':name,
            'url':url,
            'imgurl':imgurl,
            'listeners':listeners,
            'description':description,
            'currently_playing':currently_playing
          })
        }
        this.setState({urlList:url_list_arr})
      })
    })
    .catch((err) => {
      console.log("Error: "+err);
    })
  };

  getDirectStream = (url, callback) => {
    const https_url = "https"+url.substring(4)
    axios
    .get(https_url)
    .then((resp) => {
      let filename = resp.data.split('\n')[2].split('=')[1]
      console.log(filename);

      this.setState({
        playbackUrl:filename
      }, ()=> {
          callback(filename)
        })
    })
    .catch((err) => {
      console.log(err)
    })
  };

  getStreamMeta = () => {
    axios.get(this.state.soma_xml_url)
    .then((resp) => {
      xml2js.parseString(resp.data, (err, result) => {
          for(let x in result.channels.channel){
            if(result.channels.channel[x].title[0] === this.state.playbackStation){
              this.setState({currentlyPlaying:result.channels.channel[x].lastPlaying[0]})
              break
            }
          }
      });
    })
    .catch(function (error) {
      this.setState({currentlyPlaying:'Unavailable'})
    });
  };

  stopPlayback = (event) => {
      this.audioPlayer.src = ''
      this.audioPlayer.load();
      this.setState({
        'playing': false,
        'playbackStation':'',
        'currentlyPlaying':''
      })
  };

  updatePlayerInfo = () => {
    this.getStreamMeta();
    setInterval(()=>{
      this.getStreamMeta();
    }, 10000);

    this.audioPlayer.onwaiting = () => {
      console.log("Buffering...");
      this.setState({
        playing:false,
        buffering:true
      })
    }

    this.audioPlayer.onplaying = () => {
      console.log("Playing");
      this.setState({
        playing:true,
        buffering: false
      })
    }

    this.audioPlayer.ontimeupdate = () => {
      this.setState({
        timeElapsed:Math.round(this.audioPlayer.currentTime)
      })
    }
  };

  loadSelected = (event,random) => {
      let targetValue;
      if(random){
        targetValue = Math.floor(Math.random() * Object.keys(this.state.urlList).length)
      } else {
        let targetValue = event.target.value;
      }
      // required for mobile
      this.audioPlayer.load();
      console.log("name undefined? " + this.state.urlList[targetValue].name)
      // this.setState({playbackStation:this.state.urlList[targetValue].name})
      
      // decode pls file, callback once done and play the file
      this.getDirectStream(this.state.urlList[targetValue].url,(fn)=>{
        this.audioPlayer.src = this.state.playbackUrl
        this.audioPlayer.load();
        this.audioPlayer.play();
        this.updatePlayerInfo();
    })
  };

  formatTime = (seconds) => {
      let min = Math.floor(seconds / 60);
      let sec = seconds - min * 60;

      if(min < 10){ min="0"+min }
      if(sec < 10){ sec="0"+sec }

      return " ("+min.toString()+":"+sec.toString()+") "
  };

  render () {
    return (
      <div className="App">
      
        {/*audio element*/}
        <audio ref={audioPlayer => { this.audioPlayer = audioPlayer;}} />

        {/*header*/}
        <div className='Header'>
              
              <Controls 
                stopPlayback={this.stopPlayback} 
                playbackState={this.state.playing}
                bufferingState={this.state.buffering}
                random={this.loadSelected}
              />
              <p>
                {(this.state.playing) ? <b>Station </b> : <b>SomaFM ReactJS Player</b>}
                {
                  (this.state.playing) 
                  ? this.state.playbackStation
                  : ''
                }
                
                {(this.state.playing) 
                  ? this.formatTime(this.state.timeElapsed) + ' '
                  : ''
                }
                <br/>
                {(this.state.playing) 
                    ? this.state.currentlyPlaying
                    : <b>Stopped</b>
                  } 
              </p>
        </div>
        {/*station list*/}
          <StationList 
            list={this.state.urlList}
            loadSelected={this.loadSelected}
          />
        {/*footer*/}
        <Footer/>

      </div>
      )
  }
}

class Footer extends Component {
  render(){
    return(
      <div className="footer">
        <span className='footerInfo'><a className='footerName' href="https://wwww.jamesbos.com">jamesbos.com</a></span>
        <span className='footerInfo'>|</span>
        <span className='footerInfo'>view source on</span>
        <span className='footerInfo'><a href='https://www.github.com/jampola/somareactplayer'><i className='fa fa-github'/></a></span>
      </div>
      )
  }
}

class StationList extends Component {
  render(){
    return (
      <div className="StationList">
        {this.props.list.map((station, i) => {

          return (
              <div 
                className='StationInfo'
                key={i}
                style={{
                  backgroundImage: 'url(' + station.imgurl + ')',
                }}
              >
           
                <h3 className='stationTitle'>{station.name}</h3>

                <p className='currentlyPlaying'>
                  {station.currently_playing}
                </p>

                <p className='stationListeners'>
                  <b>Listeners</b> <br/>
                  {station.listeners}
                </p>

                <button
                  className="PlayButton fa fa-play" 
                  value={i}
                  onClick={this.props.loadSelected}
                  >
                </button>

              </div>
            )

          })
        }
      </div>
    )
  }
}

class Controls extends Component {
  render(){
    let button = null;

    if(this.props.playbackState){
      button = <button className="fa fa-stop StopButton" onClick={this.props.stopPlayback}></button>
    } else if(this.props.bufferingState){
      button = <button className="fa fa-refresh fa-spin fa-2x fa-fw Buffering"></button>
    } else {
      button = <button className="fa fa-random StopButton" onClick={() => this.props.random(this,true)}></button>
    }

    return (
      <div className="Controls">{button}</div>
      )
  }
}

export default App;


